*{
  font-family: 'Open Sans', sans-serif;
}
.main{
  width: 500px;
  margin: 0 auto;
}
.title{
  text-align: center;
  margin-top: 100px;
}
.filter{
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}
.list_item{
  display: flex;
  justify-content: space-between;
}
.list_item_action{
  display: flex;
}
.list_item_mark{
  margin-right: 10px;
  border: none;
}
.list_item_del{
  border: none;
}